import Vue from 'vue'
import VueAxios from 'vue-axios'
import VueAuthenticate from 'vue-authenticate'
import axios from 'axios'
import Router from 'vue-router'
import Signin from '@/components/Signin'
import Login from '@/components/Login'
import Welcome from '@/components/Welcome'

Vue.use(Router)

Vue.use(VueAuthenticate, {
  tokenName: 'access_token',
  baseUrl: 'http://localhost:4000',
  storageType: 'localStorage',
  providers: {
    github: {
          clientId: 'baefe3743fb81b6828f4',
          clientSecret: '7fc0fabb5eae3c3a2fc31632d1351e1849f76ddb',
          redirectUri: 'https://pedantic-williams-fc3e0c.netlify.com/login/#/welcome' // Your client app URL

        },
    google: {
              clientId: '1234',
              redirectUri: 'http://localhost:8080/welcome' // Your client app URL
            }

  }
})
Vue.use(VueAxios, axios)

const vueAuth = new VueAuthenticate(Vue.prototype.$http, {
  baseUrl: 'https://pedantic-williams-fc3e0c.netlify.com'
})
export default new Router({

  routes:
  [

    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/welcome',
      name: 'welcome',
      component: Welcome
    },
    {
      path: '/signin',
      name: 'signin',
      component: Signin
    }
  ]
})



