<template>

<div class="container center">

  <div v-if="logedin">
    hola {{response.data.name}}
  <div class="row">
        <div class="col s12">
          <a class="waves-effect waves-light btn" v-on:click="logout">Log out</a>
        </div>
      </div>

  </div>
  <div v-else>
  <h1>Agent access</h1>
      <div class="row">
        <form class="col offset-m4 m4 s12">
          <div class="row">
            <div class="input-field col s12">
              <input v-bind=user id="user" type="text" class="validate">
              <label for="user">User</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input v-bind=password id="password" type="password" class="validate" >
              <label for="password">Password</label>
            </div>
          </div>
          <div class="row">
            <div class="col s12">
              <a class="waves-effect waves-light btn" v-on:click="login2">Log in</a>
            </div>
          </div>
          <div class="row">
            <div class="col s12">
              <a class="waves-effect waves-light btn" v-on:click="register">Sign in</a>
            </div>
          </div>
          <button @click="authenticate('github')">auth Github</button>
          <button @click="authenticate('facebook')">auth Facebook</button>
          <button @click="authenticate('google')">auth Google</button>
          <button @click="authenticate('twitter')">auth Twitter</button>
      </form>
    </div>
      </div>

</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Login',
  data: function () {
    return {
      user: null,
      password: null,
      logedin: null,
      response: null
    }
  },
  mounted () {

    //peticion para saber que metodos oauth tiene disponible cada empresa y datos de configuracion de cada una


  },
  methods: {
    // metodo contra nuestra api de autenticacion
    login: function () {
      console.log('entra funcion')
      var d = this.$data
      var route = this.$router

         axios({
              method: 'post',
              url: globaldata.baseurl + '/auth',
              params: {
                user: d.user,
                password: d.passwordl
              }
            })
              .then(function (response) {
                console.log('correcto')
                const token = resp.data.token
                localStorage.setItem('user-token', token)
                route.push({ name: 'welcome', query: { msg: 'Hola' } })
              })
              .catch(function (error) {
                console.log('Incorrecto')
                localStorage.removeItem('vueauth_access_token')
                route.push({ name: 'signin', query: { msg: 'Adios' } })
              })
    },
    login2: function () {
      var this_ = this;
      var d = this.$data
      let user = {
        email: 'john.doe@domain.com',
        password: 'pass123456'
      };

      if (this.$auth.isAuthenticated()) {
        this.$auth.logout()
      }

      this.$auth.login(user).then(function (response) {

        console.log('llega aqui')
        if (response.status === 200) {
          d.response = response
          d.logedin = true
          d.errorLogin =  null
          console.log(localStorage.removeItem('vueauth_access_token'))
        }else{
          d.logedin = null
          d.errorLogin = "error al logarse intentelo de nuevo"
        }


      })
    },
    // registro contra nuestra apiu de autenticacion
    register: function () {

      //this.$auth.register({ name, user, password }).then(function () {
      // Execute application logic after successful registration
      //})
      this.$router.push({ name: 'welcome' })
    },
    sign: function () {
      console.log('login')
      this.$router.push({ name: 'signin' })
    },
    //logout limpia variable de entorno //revisar si hay que desconectar de algun sitio mas
    logout: function () {
      if (this.$auth.isAuthenticated()) {
        this.$auth.logout()
      }
    },
    //autenticacion contra los diferentes oauth
    authenticate: function (provider) {
          console.log('storage' + JSON.stringify(this.$auth))
          if (this.$auth.isAuthenticated()) {
            this.$auth.logout()
          }
          this.response = null
          this.$auth.authenticate(provider).then(function (authResponse) {
            console.log('response' +  JSON.stringify(authResponse))
            this.response = authResponse
          })
          .catch(function (error) {
            console.log('Incorrecto ' + error)

          })
        }
  }
}
</script>

<style scoped>

</style>



